/* --- ANA SAYFA STİLLERİ --- */
.home-page {
    padding: 80px 1.5rem 100px 1.5rem; /* Header ve Nav bar için boşluk */
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.section-title {
    font-size: 1.2rem;
    font-weight: 700;
    color: var(--text-color);
    text-align: center;
}

/* Haftalık Seri Takibi */
.streak-tracker {
    width: 100%;
    max-width: 500px; /* Görev kartı ile aynı genişlik */
    margin: 0 auto; /* Ortalama */
}

.streak-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
}

.pass-container {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

#use-pass-btn {
    background: none;
    border: 1px solid var(--secondary-color);
    color: var(--secondary-color);
    padding: 0.3rem 0.6rem;
    border-radius: 8px;
    cursor: pointer;
    font-size: 0.8rem;
}

#pass-count-display {
    font-weight: 700;
}

.streak-circles {
    display: flex;
    justify-content: space-between;
    gap: 0.5rem;
}

.streak-circle {
    flex: 1;
    aspect-ratio: 1 / 1;
    border-radius: 50%;
    background-color: var(--card-background);
    border: 3px solid var(--border-color);
    transition: all 0.4s ease;
    cursor: pointer;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.streak-circle.completed {
    background-color: var(--primary-color);
    border-color: var(--primary-color);
    transform: scale(1.05);
    box-shadow: 0 4px 15px rgba(76, 175, 80, 0.4);
}

.streak-circle.passed {
    background-color: var(--secondary-color);
    border-color: var(--secondary-color);
    transform: scale(1.05);
    box-shadow: 0 4px 15px rgba(255, 152, 0, 0.4);
}

.streak-circle.passed::before {
    content: '\f054'; /* Font Awesome forward icon */
    font-family: 'Font Awesome 5 Free';
    font-weight: 900;
    color: white;
    font-size: 1rem;
    animation: popIn 0.3s ease-out;
}

/* Günün İyilik Görevi Kartı */
.daily-task-card {
    background: var(--card-background);
    border-radius: 20px;
    padding: 2rem;
    text-align: center;
    box-shadow: 0 10px 30px var(--shadow-light);
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
}

.task-passed-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 152, 0, 0.9);
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.4s ease;
    z-index: 5;
}

.task-passed-overlay.active {
    opacity: 1;
    pointer-events: all;
}

.task-passed-overlay i {
    font-size: 3rem;
    margin-bottom: 1rem;
}

.task-passed-overlay span {
    font-size: 1.5rem;
    font-weight: 700;
}

.task-card-content h3 {
    font-size: 1rem;
    font-weight: 600;
    color: var(--light-text-color);
    margin-bottom: 1rem;
}

.task-card-content p {
    font-size: 1.5rem;
    font-weight: 700;
    color: var(--text-color);
    min-height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 2rem;
}

.task-buttons {
    display: flex;
    gap: 1rem;
}

#prove-task-btn, #refresh-task-btn {
    flex: 1;
    padding: 1rem;
    border: none;
    border-radius: 15px;
    font-size: 1.1rem;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.3s ease;
}

#prove-task-btn {
    background: linear-gradient(45deg, var(--primary-color), #81c784);
    color: var(--white-color);
    box-shadow: 0 4px 20px rgba(76, 175, 80, 0.3);
}

#prove-task-btn:hover {
    transform: translateY(-3px);
    box-shadow: 0 7px 25px rgba(76, 175, 80, 0.4);
}

#refresh-task-btn {
    background-color: #f0f2f5;
    color: var(--light-text-color);
}

#prove-task-btn i, #refresh-task-btn i {
    margin-right: 0.5rem;
}

/* Alt Navigasyon Barı (Mobil) */
.bottom-nav {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 80px;
    background-color: var(--card-background);
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-shadow: 0 -5px 20px var(--shadow-light);
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    z-index: 10;
}

.nav-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    color: var(--light-text-color);
    font-size: 0.8rem;
    transition: all 0.3s ease;
}

.nav-item i {
    font-size: 1.5rem;
    margin-bottom: 0.25rem;
}

.nav-item.active {
    color: var(--primary-color);
    transform: translateY(-5px);
}

/* Animasyonlar */
.animate-fade-in {
    animation: fadeIn 0.8s ease-out forwards;
}

.animate-slide-up {
    animation: slideUp 0.8s ease-out forwards;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes slideUp {
    from { opacity: 0; transform: translateY(20px); }
    to { opacity: 1; transform: translateY(0); }
}

@keyframes popIn {
    from { transform: scale(0.5); opacity: 0; }
    to { transform: scale(1); opacity: 1; }
}

/* Web/Mobil için Header ve Footer */
.app-header {
    width: 100%;
    background-color: var(--card-background);
    box-shadow: 0 2px 10px var(--shadow-light);
    padding: 1rem 1.5rem;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
}
.header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
}
.logo-container {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}
.header-logo {
    width: 40px;
    height: 40px;
}
.settings-menu {
    position: relative;
}
.settings-btn {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--text-color);
}
.dropdown-menu {
    display: none;
    position: absolute;
    right: 0;
    top: 120%;
    background-color: var(--card-background);
    border-radius: 10px;
    box-shadow: 0 5px 15px var(--shadow-light);
    overflow: hidden;
    z-index: 101;
}
.dropdown-menu.show {
    display: block;
}
.dropdown-menu a {
    display: block;
    padding: 0.75rem 1.5rem;
    text-decoration: none;
    color: var(--text-color);
    white-space: nowrap;
}
.dropdown-menu a:hover {
    background-color: #f0f2f5;
}

.app-footer {
    display: none; /* Mobilde gizli, web'de gösterilecek */
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 1.5rem 2rem;
    background-color: #333;
    color: var(--white-color);
}
.footer-links a {
    color: var(--white-color);
    text-decoration: none;
    margin-right: 1.5rem;
}
.social-media-links a {
    color: var(--white-color);
    font-size: 1.5rem;
    margin-left: 1.5rem;
}

@media (min-width: 769px) {
    .bottom-nav {
        display: none; /* Web'de gizli */
    }
    .app-footer {
        display: flex; /* Web'de göster */
    }
}

@media (max-width: 768px) {
    .home-page {
        padding: 80px 1rem 100px 1rem;
    }
    .header-content h1 {
        display: none; /* Mobilde başlığı gizle */
    }
    .section-title {
        font-size: 1.1rem;
    }
    .streak-circle {
        width: auto;
    }
    .daily-task-card {
        padding: 1.5rem;
    }
    .task-card-content p {
        font-size: 1.3rem;
    }
}